<template>
<!--  eslint-disable -->
  <div>
    <validation-observer 
      ref="profile"
      #default="{invalid}">
      <!-- Modal para Agregar Perfil -->
      <b-modal
        id="modal-1"
        ref="profileModal"
        hide-footer
        title="Agregar Perfil"
        scrollable
        @hidden="hideProfileModal"
      >
      <ErrorValidation v-if="validationErrors" :errors="validationErrors" />
        <div class="d-block">
          <form @submit.prevent="addProfile">
            <div class="form-row">

              <!-- Nombre Perfil -->
              <div class="form-group col-md-12">
                <label for="name">Nombre</label>
                <validation-provider
                    #default="{ errors }"
                    name="nombre"
                    rules="required|alpha_accents"
                  >
                  <b-form-input
                    id="name"
                    v-model="profile.name"
                    type="text"
                    class="form-control"
                    :state="errors.length > 0 ? false:null"
                    placeholder="Nombre del Perfil"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </div>

            </div>

            <hr>

            <div class="text-right">
              <button
                type="button"
                class="btn btn-danger btn-sm mx-1"
                @click="hideProfileModal"
              >
                Cancelar
              </button>
              <button
                type="submit"
                class="btn btn-primary btn-sm"
                :disabled="invalid"
              >
                <span class="fa fa-check" /> Guardar
              </button>
            </div>
          </form>
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/* eslint-disable */

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Servicios
import * as profileService from '@/services/profile'

export default {

  name: 'AddProfile',

  components: {
    ToastificationContent,
  },

  data() {
    return {
      validationErrors: '',
      profile: {
        name: '',
      },
    }
  },

  methods: {

    async addProfile() {
      try {
        const response = await profileService.addProfile(this.profile)

        if (response.status === 200) {
          this.$emit('reload')
          this.hideProfileModal()
          this.showToast('Agregado', 'CheckIcon', 'Perfil Agregado', 'success')
        }
      } catch (error) {
          if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
          } else {
            this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al agregar el perfil', 'warning')
          }
      }
    },

    hideProfileModal() {
      this.$refs.profile.reset()
      this.$refs.profileModal.hide()
      this.cleanForm()
    },

    cleanForm() {
      this.validationErrors = ''
      this.profile = { name: '' }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },
}

</script>

